import { Header } from 'components/Header';
import React from 'react';
import header_banner from '../../assets/work/header_banner.png';
import { Breadcrumbs } from 'components/Breadcrumbs';
import { WorkMain } from './WorkComponents/index';

import vector from 'assets/work/Vector.png';
import vector1 from 'assets/work/Vector1.png';
import vector2 from 'assets/work/Vector2.png';
import vector3 from 'assets/work/Vector3.png';
import vector4 from 'assets/work/Vector4.png';
import group from 'assets/work/Group.png';
import { Button } from 'ui/Button';
import axios from 'axios';
import { Link } from 'react-router-dom';

export const workHaeader = {
  title: '  ',
  text: '     , -  ',
  img: header_banner,
};

export const Work = () => {
  const [data, setData] = React.useState([]);
  async function fetchData() {
    const response = await axios.get('https://hack-solution.tech/api/work/');
    setData(response.data);
  }

  React.useEffect(() => {
    fetchData();
  }, []);
  return (
    <>
      <Header {...workHaeader} whiteBackground>
        <div className="header__btn">
          <Link to="/about/work/allvacancies">
            <Button icon>  </Button>
          </Link>
        </div>
      </Header>

      <main>
        <Breadcrumbs breadcrumbs={['', '  ']} />
        <div className="container">
          <WorkMain data={data} />
        </div>
      </main>
    </>
  );
};
