import React from 'react';
import header_img from 'assets/work/image443.png';
import { Header } from 'components/Header';
import { Breadcrumbs } from 'components/Breadcrumbs';

import jobResponsibilitiesImg from 'assets/work/vacancy_img.png';
import requirementsImg from 'assets/work/vacancy_vector1.png';
import conditionsImg from 'assets/work/vacancy_vector.png';
import { Link, useParams } from 'react-router-dom';
import axios from 'axios';
import { Button } from 'ui/Button';

export function Vacancy() {
  const [data, setData] = React.useState();

  const { id } = useParams();

  async function fetchData() {
    const response = await axios.get(`https://hack-solution.tech/api/work/vacancy/${id}`);
    setData(response.data);
    console.log(response.data);
  }

  React.useEffect(() => {
    fetchData();
  }, []);

  const headerProps = {
    title: '',
    text: '     , -  ',
    img: data?.image,
  };
  return (
    <>
      <Header {...headerProps} whiteBackground />
      <main>
        <Breadcrumbs
          breadcrumbs={[
            '',
            '  ',
            '',
            '-    ',
          ]}
        />

        <div className="container">
          <h1 className="vacancy_page_title">{data?.title}</h1>
          <h3 className="vacancy_page_salary"> {data?.salary} .</h3>

          <div className="job_disc">
            <p>
                : <span>{data?.experience} </span>
            </p>
            <p></p>
          </div>

          <section className="jobResponsibilities">
            <h2> </h2>

            <ul className="jobResponsibilities__list">
              {data?.list_position.map((item, idx) => (
                <li key={idx}>
                  <img src={jobResponsibilitiesImg} alt="" />
                  <span>{item}</span>
                </li>
              ))}
            </ul>

            <h2></h2>

            <ul className="jobResponsibilities__list">
              {data?.list_conditions.map((item, idx) => (
                <li key={idx}>
                  <img src={conditionsImg} alt="" />
                  <span>{item}</span>
                </li>
              ))}
            </ul>

            <h2></h2>

            <ul className="jobResponsibilities__list">
              {data?.list_requirements.map((item, idx) => (
                <li key={idx}>
                  <img style={{ width: '6px', height: '22px' }} src={requirementsImg} alt="" />
                  <span>{item}</span>
                </li>
              ))}
            </ul>
          </section>
          <div className="feedBackBlock-v">
            <p>
                  : <span>+7 (929) 128-16-76</span>
            </p>
            <p>
               : <span>hr@avanta-service.com</span>
            </p>

            <Link to={`/about/work/jobPosting/${id}`}>
              <Button></Button>
            </Link>
          </div>
        </div>
      </main>
    </>
  );
}
