import styles from './textField.module.scss';
import classNames from 'classnames';

export function TextField({ type, value, onChange, label, placeholder, small, fullWidth }) {
  return (
    <div>
      <label htmlFor={label} className={styles.field_label}>
        {label}
      </label>
      <input
        type={type}
        value={value}
        onChange={onChange}
        placeholder={placeholder}
        className={classNames(styles.textField, {
          [styles.small]: small,
          [styles.fullWidth]: fullWidth,
        })}
      />
    </div>
  );
}
