import React from "react";

export const Header = ({
  title,
  subtitle,
  text,
  img,
  whiteBackground,
  children,
}) => {
  const isWhiteBackground = (isWhite) => {
    return isWhite ? (
      <div className="header__innerWhite">
        <div className="header__whiteBackground">
          <div className="header__titles">
            <h1 className="header__title">{title}</h1>
            <h1 className="header__title">{subtitle}</h1>
          </div>
          <div>
            <h6 className="header__text">{text}</h6>
          </div>
        </div>
        {children}
      </div>
    ) : (
      <div className="header__inner">
        <div className="header__titles">
          <h1 className="header__title">{title}</h1>
        </div>
        <div>
          <h6 className="header__text">{text}</h6>
        </div>
        {children}
      </div>
    );
  };

  return (
    <header
      className="header"
      style={{
        backgroundImage: `url(${img})`,
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
      }}
    >
      {isWhiteBackground(whiteBackground)}
    </header>
  );
};
