import { Routes, Route } from 'react-router-dom';
import Layout from 'Layouts/Layout';
import UseResetScroll from 'hooks/useResetScroll';
import { About } from 'pages/About/About';
import { Home } from 'pages/Home/Home';
import { Services } from 'pages/Services';
import { Projects } from 'pages/Projects';
import { Protection } from 'pages/Protection';
import { Contacts } from 'pages/Contacts';
import { Partners } from 'pages/About/Partners';
import { Work } from 'pages/About/Work';
import { Recomendation } from 'pages/About/Recomendation';
import { License } from 'pages/About/License';
import { Equipment } from 'pages/About/Equipment';
import { ResponseStudents, AllVacancies, Vacancy, JobPosting } from './pages/About/WorkComponents';
import { ServicesItem } from 'pages/ServicesItem';
import { ProjectsItem } from 'pages/ProjectsItem';

function App() {
  return (
    <>
      <UseResetScroll />
      <Routes>
        <Route path="/" element={<Layout />}>
          <Route path="" element={<Home />} />
          <Route path="/about" element={<About />} />
          <Route path="/about/partners" element={<Partners />} />
          <Route path="/about/work" element={<Work />} />
          <Route path="/about/work/response/:id" element={<ResponseStudents />} />
          <Route path="/about/work/allvacancies" element={<AllVacancies />} />
          <Route path="/about/work/jobPosting/:id" element={<JobPosting />} />
          <Route path="/about/work/vacancy/:id" element={<Vacancy />} />
          <Route path="/about/recomendation" element={<Recomendation />} />
          <Route path="/about/license" element={<License />} />
          <Route path="/about/equipment" element={<Equipment />} />
          <Route path="/services" element={<Services />} />
          <Route path="/services/:id" element={<ServicesItem />} />
          <Route path="/projects" element={<Projects />} />
          <Route path="/protection" element={<Protection />} />
          <Route path="/contacts" element={<Contacts />} />
        </Route>
      </Routes>
    </>
  );
}

export default App;
