import { Breadcrumbs } from "components/Breadcrumbs";
import { DropdownModalList } from "components/DropdownModalList";
import { Header } from "components/Header";
import { licenseHaeader, licenselist } from "./constans";

export const License = () => {
  return (
    <>
      <Header {...licenseHaeader} whiteBackground />
      <main>
        <Breadcrumbs breadcrumbs={["", "  "]} />
        <div className="container">
          <DropdownModalList lists={licenselist} />
        </div>
      </main>
    </>
  );
};
