import React from "react";
import { Slider } from "components/slider";
import { ListWithSubtitle } from "components/ListWithSubtitle";
import { Header } from "components/Header";
import { partners, services } from "./constants";
import { main } from "./constants";
import { PartnerCard } from "components/PartnerCard";
import { Link, useNavigate } from "react-router-dom";

export const Home = () => {
  const navigate = useNavigate();

  return (
    <>
      <Header {...main} whiteBackground={false} />
      <main>
        <div className="container">
          <h2 className="container__title"> </h2>
          <Slider />
        </div>
        <div className="container">
          <h2 className="container__title"></h2>
          <div className="services">
            <ListWithSubtitle lists={services} />
            <button
              onClick={() => navigate("/services")}
              className="services__button button"
            >
               
              <svg
                width="24"
                height="24"
                viewBox="0 0 24 24"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M17 12C17 11.712 16.8978 11.447 16.7035 11.2396L8.60532 2.29954C8.42127 2.10369 8.19632 2 7.93047 2C7.409 2 7 2.44931 7 3.04839C7 3.33641 7.10225 3.60138 7.26585 3.79724L14.7096 12L7.26585 20.2028C7.10225 20.3986 7 20.6521 7 20.9516C7 21.5507 7.409 22 7.93047 22C8.19632 22 8.42127 21.8963 8.60532 21.6889L16.7035 12.7604C16.8978 12.5415 17 12.288 17 12Z"
                  fill="white"
                />
              </svg>
            </button>
            <div className="services__img"></div>
            {/* <img src={iconAvanta} alt="" /> */}
          </div>
        </div>
        <div className="container">
          <div className="container__linkTitle">
            <h2 className="container__title"> !</h2>
            <Link to="about/partners" className="link">
               
            </Link>
          </div>
          <div className="partners">
            {partners.map((partner, index) => (
              <PartnerCard {...partner} title={partner.title} key={index} />
            ))}
          </div>
        </div>
      </main>
    </>
  );
};
