import { Header } from 'components/Header';
import React from 'react';

import header_img from 'assets/work/job_posting_img.png';
import { TextField } from 'ui/TextField';
import axios from 'axios';
import { useParams } from 'react-router';
import { Button } from 'ui/Button';
import { SuccessScreen } from '.';

export function JobPosting() {
  const headerProps = { title: '  ', text: '', img: header_img };

  const [values, setValues] = React.useState({
    name: '',
    phone: '',
    email: '',
    disc: '',
  });
  const [isSuccess, setIsSuccess] = React.useState(false);
  const { id } = useParams();

  console.log(id);

  async function handleJobPosting() {
    try {
      const response = await axios({
        method: 'POST',
        url: `https://hack-solution.tech/api/work/user_vacancy/`,
        data: {
          name: values.name,
          number: values.phone,
          mail: values.email,
          course_of_study: values.disc,
          vacancy: id,
        },
      });
      console.log(response.data);
      setIsSuccess(true);
    } catch (err) {
      alert(err);
    }
  }
  return (
    <>
      <Header {...headerProps} whiteBackground />

      <main>
        <div className="container">
          {isSuccess ? (
            <SuccessScreen />
          ) : (
            <>
              <div className="smalls__fields">
                <TextField
                  value={values.name}
                  type={'text'}
                  onChange={(e) => setValues((prev) => ({ ...prev, name: e.target.value }))}
                  placeholder={''}
                  label={'*'}
                  small
                />
                <TextField
                  value={values.phone}
                  onChange={(e) => setValues((prev) => ({ ...prev, phone: e.target.value }))}
                  type={'phone'}
                  placeholder={'+7 (___) ___-__-__'}
                  label={'*'}
                  small
                />
              </div>
              <div>
                <TextField
                  value={values.email}
                  type={'email'}
                  onChange={(e) => setValues((prev) => ({ ...prev, email: e.target.value }))}
                  placeholder={'Ivan1888@mail.ru'}
                  label={'*'}
                  fullWidth
                />
                <p className="coveringletter"> </p>
                <textarea
                  value={values.disc}
                  onChange={(e) => setValues((prev) => ({ ...prev, disc: e.target.value }))}
                  className="textarea"
                ></textarea>
              </div>
              <div>
                <p className="fields__disc">
                     "",       
                        
                </p>

                <Button onClick={handleJobPosting}></Button>
              </div>
            </>
          )}
        </div>
      </main>
    </>
  );
}
