import React from 'react';
import { Header } from 'components/Header';
import projectsBackground from 'assets/headerPic/Projects.png';
import { Breadcrumbs } from 'components/Breadcrumbs';
import axios from 'axios';

export const projectsHaeader = {
  title: '',
  text: '   -,   ',
  img: projectsBackground,
};

export const Projects = () => {
  const [data, setData] = React.useState([]);

  async function fetchData() {
    try {
      const response = await axios.get('http://hack-solution.tech/api/projects/');
      setData(response.data);
    } catch (err) {
      alert(err);
    }
  }

  React.useEffect(() => {
    fetchData();
  }, []);
  return (
    <>
      <Header {...projectsHaeader} whiteBackground />
      <main>
        <Breadcrumbs breadcrumbs={['']} />
        <div className="container">
          {data.map((item) => (
            <>
              {item.section.map((elem) => (
                <div className="prejects_block">
                  <h2 className="title">{elem.title}</h2>

                  <div className="project">
                    {elem.block.map((el) => (
                      <div key={el.title} className="project__block">
                        <img className="image" src={el.image} alt="image" />
                        <p className="block-title">{el.title}</p>
                      </div>
                    ))}
                  </div>
                </div>
              ))}
            </>
          ))}
        </div>
      </main>
    </>
  );
};
