import { Header } from 'components/Header';
import protectionBackground from 'assets/headerPic/Protection.png';
import { Breadcrumbs } from 'components/Breadcrumbs';
import { List } from 'components/List';

export const protectionHaeader = {
  title: ' ',
  text: ' ',
  img: protectionBackground,
};

export const protection = [
  {
    title: '     25.12.2019',
  },
  {
    title: '       25.12.2019',
  },
  {
    title: '     01.06.2020',
  },
  {
    title: '       01.06.2020',
  },
  {
    title: '     14.09.2020',
  },
  {
    title: '       14.09.2020',
  },
  {
    title: '     19.11.2020',
  },

  {
    title: '       19.11.2020',
  },
  { title: '     07.04.2022' },
  { title: '       07.04.2022' },
  { title: `     "" ` },
];

export const Protection = () => {
  return (
    <>
      <Header {...protectionHaeader} whiteBackground />
      <main>
        <Breadcrumbs breadcrumbs={[' ']} />
        <div className="container">
          <section className="protection">
            <div>
              {protection.slice(0, 6).map((item, idx) => (
                <div className="element" key={idx}>
                  <div className="tochka_div">
                    <div className="tochka"></div>
                  </div>
                  <p className="protection-text">{item.title}</p>
                </div>
              ))}
            </div>
            <div>
              {protection.slice(6).map((item, idx) => (
                <div className="element" key={idx}>
                  <div className="tochka_div">
                    <div className="tochka"></div>
                  </div>
                  <p className="protection-text">{item.title}</p>
                </div>
              ))}
            </div>
          </section>
        </div>
      </main>
    </>
  );
};
