import { Breadcrumbs } from 'components/Breadcrumbs';
import { Header } from 'components/Header';
import { List } from 'components/List';
import { aboutHaeader, directions } from './constans';
import img from 'assets/pic/About2.png';

export const About = () => {
  return (
    <>
      <Header {...aboutHaeader} whiteBackground />
      <main>
        <Breadcrumbs breadcrumbs={['', ' ']} />
        <div className="container">
          <h2 className="container__title">
              <br />  
          </h2>
          <div className="about">
            <div>
              {directions.map((item, idx) => (
                <div className="element" key={idx}>
                  <div className="tochka_div">
                    <div className="tochka"></div>
                  </div>
                  <p className="protection-text">{item.title}</p>
                </div>
              ))}
            </div>
            <div className="about__img"></div>
          </div>
        </div>
        <div className="container">
          <div className="aboutSection">
            <img className="aboutSection__img" src={img} alt="About" />
            <div className="aboutSection__paragraph">
              <p className="aboutSection__title">     </p>
              <p className="aboutSection__text">
                           .
                  ,      .{' '}
                <br />    ,   .
              </p>
            </div>
          </div>
        </div>
      </main>
    </>
  );
};
