import { SliderMobile } from "./slider-mobile";
import { SliderDesktop } from "./slider-desctop/slider-desctop";
import { useMediaValue } from "../../hooks/use-media-value";
import { navItems, slides } from "./slider.constants";

export function Slider() {
  const SliderComponent = useMediaValue({
    xs: <SliderMobile navItems={navItems} slides={slides} />,
    md: <SliderDesktop navItems={navItems} slides={slides} />,
  });
  

  return SliderComponent;
}
