import Arrow from "assets/icons/BlackArrow.svg";
import classNames from "classnames";
import license from "assets/license/licence.png";
import { useState } from "react";
import { ModalCard } from "./ModalCard";

export const DropdownModalList = ({ lists }) => {
  const [active, setActive] = useState([]);

  const handleCheckActive = (index) => {
    if (active.includes(index)) {
      setActive(active.filter((idx) => idx !== index));
    } else {
      setActive([...active, index]);
    }
  };

  return (
    <ul className="dropdown">
      {lists.map((list, idx) => (
        <li key={idx}>
          <div className="dropdown__drop">
            <h3
              onClick={() => handleCheckActive(idx)}
              className="dropdown__title"
            >
              {list.title}
            </h3>
            <img
              onClick={() => handleCheckActive(idx)}
              className={classNames("dropdown__arrow", {
                "dropdown__arrow--active": active.includes(idx),
              })}
              src={Arrow}
              alt="Arrow"
            />
          </div>
          <div
            className={classNames("dropdown__item", {
              "dropdown__item--active": active.includes(idx),
            })}
          >
            <ModalCard img={license} />
          </div>
        </li>
      ))}
    </ul>
  );
};
