import Logo from "assets/icons/MobileLogo.svg";
import Menu from "assets/icons/Menu.svg";
import Close from "assets/icons/Close.svg";
import { Link, useLocation } from "react-router-dom";
import { useEffect, useState } from "react";
import classNames from "classnames";

export const MenuSidebar = () => {
  const [active, setActive] = useState(false);
  const { pathname } = useLocation();

  useEffect(() => {
    setActive(false);
  }, [pathname]);

  return (
    <nav
      className={classNames("menuSidenav", {
        "menuSidenav--active": active,
      })}
    >
      <Link to="/" className="menuSidenav__logoLink">
        <img className="menuSidenav__logo" src={Logo} alt="Logo" />
      </Link>
      {active ? (
        <img
          src={Close}
          onClick={() => setActive((e) => !e)}
          className="menuSidenav__menuIcon"
          alt="menu"
        />
      ) : (
        <img
          src={Menu}
          onClick={() => setActive((e) => !e)}
          className="menuSidenav__menuIcon"
          alt="menu"
        />
      )}
      <div className="menuSidenav__navLinks">
        <ul>
          <li>
            <Link className="menuSidenav__link" to="/">
               
            </Link>
          </li>
          <li className="menuSidenav__dropdown">
            <div className="menuSidenav__disable">
              <Link className="menuSidenav__link menuSidenav__disableLink" to="about">
                 
              </Link>
            </div>
            <ul className="menuSidenav__submenu">
              <li>
                <Link className="menuSidenav__link menuSidenav__sublink" to="about/work">
                    
                </Link>
              </li>
              <li>
                <Link className="menuSidenav__link menuSidenav__sublink" to="about/recomendation">
                  
                </Link>
              </li>
              <li>
                <Link className="menuSidenav__link menuSidenav__sublink" to="about/license">
                    
                </Link>
              </li>
              <li>
                <Link className="menuSidenav__link menuSidenav__sublink" to="about/partners">
                  
                </Link>
              </li>
              <li>
                <Link className="menuSidenav__link menuSidenav__sublink" to="about/equipment">
                  
                </Link>
              </li>
            </ul>
          </li>
          <li>
            <Link className="menuSidenav__link" to="services">
              
            </Link>
          </li>
          <li>
            <Link className="menuSidenav__link" to="projects">
              
            </Link>
          </li>
          <li>
            <Link className="menuSidenav__link" to="protection">
               
            </Link>
          </li>
          <li>
            <Link className="menuSidenav__link" to="contacts">
              
            </Link>
          </li>
        </ul>
      </div>
    </nav>
  );
};
