import Close from "assets/icons/ModalClose.svg";
import { createPortal } from "react-dom";

export const Modal = ({ isOpened, onClose, children }) => {
  if (!isOpened) return null;

  return createPortal(
    <div className="modal">
      <img
        className="modal__closeIcon"
        onClick={onClose}
        src={Close}
        alt="close"
      />
      <div onClick={onClose} className="modal__overlay" />
      <div className="modal__content">{children}</div>
    </div>,
    document.body
  );
};
