import { Breadcrumbs } from "components/Breadcrumbs";
import { DropdownModalList } from "components/DropdownModalList";
import { Header } from "components/Header";
import { recomendationHaeader, recomendlist } from "./constans";

export const Recomendation = () => {
  return (
    <>
      <Header {...recomendationHaeader} whiteBackground />
      <main>
        <Breadcrumbs breadcrumbs={["", ""]} />
        <div className="container">
          <DropdownModalList lists={recomendlist} />
        </div>
      </main>
    </>
  );
};
