import React from "react";

export const ListWithSubtitle = ({ lists }) => {
  // console.log(lists[0]?.subtitles);
  return (
    <div className="list">
      <div className="list__line"></div>
      <ul className="list__titles">
        {lists.map((list, index) => (
          <li key={index}>
            {/* <img src="../../assets/TitleEllipse.svg" alt="" /> */}
            <p className="list__title">{list.title}</p>
            {list?.subtitles && (
              <ul>
                {list.subtitles.map((subtitle, index) => (
                  <li key={index}>
                    <p className="list__subtitle">{subtitle}</p>
                  </li>
                ))}
              </ul>
            )}
          </li>
        ))}
      </ul>
    </div>
  );
};
