import React from 'react';

import image4 from 'assets/work/Rectangle 15.png';
import image5 from 'assets/work/Rectangle 16.png';

import iconAvanta from 'assets/work/Icon avanta.png';
import Arrow from 'assets/work/Icon arrow.svg';

import { Link } from 'react-router-dom';
import { Button } from 'ui/Button';

export const protection = [
  {
    title: '     ',
    img: Arrow,
  },
  {
    title: ' ',
    img: Arrow,
  },
  {
    title: '   ',
    img: Arrow,
  },
  {
    title: ' ',
    img: Arrow,
  },
  {
    title: ' ',
    img: Arrow,
  },
  {
    title: '   ',
    img: Arrow,
  },
];
export function WorkMain({ data }) {
  const newArr = data.map((item) =>
    item.services.map((el, idx) => {
      const isEvenIndex = idx % 2 === 0;

      if (isEvenIndex) {
        return (
          <div className="corporateCulture__info">
            <img src={el.image} alt="image1" />
            <div>
              <h3>{el.title}</h3>
              <p>{el.description}</p>
            </div>
          </div>
        );
      }
      return (
        <div className="corporateCulture__info">
          <div>
            <h3>{el.title}</h3>
            <p>{el.description}</p>
          </div>
          <img src={el.image} alt="image1" />
        </div>
      );
    }),
  );
  return (
    <>
      {data?.map((item) => (
        <>
          {item.offers.map((el) => (
            <div key={el.id}>
              <h1>{el.title}</h1>

              <div className="list">
                <div className="list__line list__secondLine"></div>
                <ul className="list__titles">
                  <li className="list__titles__li">
                    <p className="list__title">{el.description}</p>
                    <img src={Arrow} className="sidenav__arrow" alt="img" />
                  </li>
                </ul>
              </div>
            </div>
          ))}

          <section className="corporateCulture">
            <h2 className="corporateCulture__title"> </h2>

            {newArr}
          </section>

          <section className="ourValuesBlock">
            <h2 className="ourValuesBlock__title"> </h2>

            <ul className="ourValuesBlock__ul">
              {item.values.map((item) => (
                <li key={item.id}>
                  <img src={item.image} alt="" />
                  <span>{item.description}</span>
                </li>
              ))}
            </ul>

            <div className="response">
              <div className="response__text">
                <h2>      !</h2>
                <p>
                         
                        
                </p>
                <Link className="header__btn" to={`/about/work/response/${item.id}`}>
                  <Button icon></Button>
                </Link>
              </div>
              <div className="response__images">
                <img src={image5} alt="" className="response__image5" />
                <img src={image4} alt="image2" className="response__image4" />
              </div>
            </div>
          </section>

          <section className="jobs">
            <h2 className="jobs__title"></h2>

            <div className="jobs__block">
              <div className="jobs__block__first">
                {item.list_vacancy.map((el) => (
                  <div key={el.id}>
                    <h3 className="jobs__item_title">{el.location}</h3>

                    <div className="jobs__discription">
                      <p>{el.title}</p>
                    </div>
                  </div>
                ))}
                <div className="d">
                  <Link to="/about/work/allvacancies">
                    <Button icon>  </Button>
                  </Link>
                </div>
              </div>

              <div className="jobs__block__second">
                <img src={iconAvanta} alt="iconAvanta" />
                <p>
                      : <span>+7 (929) 128-16-76</span>
                </p>
                <p>
                   : <span>hr@avanta-service.com</span>
                </p>
              </div>
            </div>
          </section>
        </>
      ))}
    </>
  );
}
