import React from 'react';

import { Header } from 'components/Header';
import { Breadcrumbs } from 'components/Breadcrumbs';

import header_img from 'assets/work/image443.png';
import hh_logo from 'assets/work/hh_logo.png';
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';

export function AllVacancies() {
  const headerProps = {
    title: '',
    text: '     , -  ',
    img: header_img,
  };

  const [data, setData] = React.useState([]);
  async function fetchData() {
    const response = await axios.get('https://hack-solution.tech/api/work/');
    setData(response.data);
  }

  React.useEffect(() => {
    fetchData();
  }, []);

  const navigate = useNavigate();
  return (
    <>
      <Header {...headerProps} whiteBackground />

      <main>
        <Breadcrumbs breadcrumbs={['', '  ', '']} />

        <div className="container">
          <div className="vacancies__disc">
            <img src={hh_logo} alt="hh.ru" />
            <p>
                 ,        
              {' '}
              <Link to="https://hh.ru" target="_blanck">
                {' '}
                HH.ru
              </Link>
            </p>
          </div>

          <section className="vacancies">
            {data.map((item) => (
              <>
                {item.list_vacancy.map((el) => (
                  <div className="vacancies__items" key={el.id}>
                    <h3 className="vacancies__title">{el.location}</h3>

                    <div
                      className="vacancies__block"
                      onClick={() => navigate(`/about/work/vacancy/${el.id}`)}
                    >
                      {el.title}
                    </div>
                  </div>
                ))}
              </>
            ))}
          </section>
        </div>
      </main>
    </>
  );
}
