import cn from "classnames";
import React from 'react'
import { useEffect, useRef, useState } from "react";

export function SliderMobile({ navItems, slides }) {
  const slidesRef = useRef(null);
  const [slideNumber, setSlideNumber] = useState(0);

  useEffect(() => {
    function handleScrollChange(e) {
      if (slidesRef.current === null) {
        return;
      }
      const currentTarget = e.currentTarget;

      const cardWidth = Math.trunc(
        slidesRef.current.scrollWidth / slides.length
      );
      const leftOffset = Math.trunc(currentTarget.scrollLeft);

      // console.log(leftOffset, cardWidth);

      // // if (Math.trunc(cardWidth) === leftOffset) {
      // //   console.log("new position");
      // // }
      // console.log(leftOffset, cardWidth);
      // if (leftOffset % cardWidth === 0) {
      //   console.log("new position", leftOffset / cardWidth);
      // }
    }

    slidesRef.current?.addEventListener("scroll", handleScrollChange);

    return () => {
      slidesRef.current?.removeEventListener("scroll", handleScrollChange);
    };
  }, []);

  return (
    <div className="m-slider">
      <div ref={slidesRef} className="m-slider__slides">
        {slides.map(({ id, subtitle }, idx) => (
          <div key={id} className="m-slider__slide">
            <div
              className={cn("m-slider__slide__top", {
                "m-slider__slide__top--active": slideNumber === idx,
              })}
            >
              {navItems[idx].name}
            </div>
            <div className="m-slider__slide-content">
              <p className="m-slider__slide-content-subtitle">{subtitle}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
