import { useEffect, useState } from "react";

const mediaPoints = {
  lg: 1440,
  md: 1024,
  sm: 767,
  xs: 599
};

function getResultSize() {
  let resultSize;
  const currentWidth = window.innerWidth;

  if (currentWidth < mediaPoints.xs) resultSize = "xs";
  else if (currentWidth < mediaPoints.sm) resultSize = "sm";
  else if (currentWidth < mediaPoints.md) resultSize = "md";
  else resultSize = "lg";

  return resultSize;
}

function getValue(size, values) {
  const { xs } = values;

  function iff(key, value) {
    return values.hasOwnProperty(key) ? values[key] : value;
  }

  switch (size) {
    case "xs":
      return xs;
    case "sm":
      return iff("sm", xs);
    case "md":
      return iff("md", iff("sm", xs));
    case "lg":
      return iff("lg", iff("md", iff("sm", xs)));
    default:
      return xs;
  }
}

const handlers = [];
let currentSize = getResultSize();

window.addEventListener("resize", () => {
  const resultSize = getResultSize();

  if (resultSize !== currentSize) {
    currentSize = resultSize;
    handlers.forEach((handler) => handler(currentSize));
  }
});

export function useMediaValue(values) {
  const [value, setValue] = useState(getValue(getResultSize(), values));

  useEffect(() => {
    const handler = (size) => setValue(getValue(size, values));

    handlers.push(handler);

    return () => {
      const index = handlers.findIndex((h) => h === handler);

      if (index !== -1) {
        handlers.splice(index, 1);
      }
    };
  }, [values]);

  return value;
}