import React from 'react';
import { Breadcrumbs } from 'components/Breadcrumbs';
import { Header } from 'components/Header';
import servicesBackground from 'assets/headerPic/Services.png';
import axios from 'axios';
import { Button } from 'ui/Button';
import { Link, useNavigate, useParams } from 'react-router-dom';

export const servicesHaeader = {
  title: '',
  text: '   ',
  img: servicesBackground,
};

export const Services = () => {
  const [projectData, setProjectData] = React.useState([]);

  async function fetchData() {
    try {
      const response = await axios.get('https://hack-solution.tech/api/service/');
      setProjectData(response.data);
      console.log(response.data);
    } catch (error) {}
  }

  React.useEffect(() => {
    fetchData();
  }, []);

  const newArray = projectData?.map((item) =>
    item.section.map((el, idx) => {
      const isEvenIndex = idx % 2 === 0;

      if (isEvenIndex) {
        return (
          <div className="isEvenIndex">
            <div className="isEvenIndex__block">
              <h2 className="isEvenIndex__block__title">{el.title}</h2>
              <Link to={`/services/${el.id}`}>
                <Button icon></Button>
              </Link>
            </div>
            <img src={el.image} alt="" />
          </div>
        );
      }
      return (
        <div className="isEvenIndex notEvenIndex">
          <img src={el.image} alt="" />
          <div className="isEvenIndex__block">
            <h2 className="isEvenIndex__block__title notEvenIndex__title">{el.title}</h2>
            <Link to={`/services/${el.id}`}>
              <Button icon></Button>
            </Link>
          </div>
        </div>
      );
    }),
  );
  return (
    <>
      <Header {...servicesHaeader} whiteBackground />
      <main>
        <Breadcrumbs breadcrumbs={['']} />

        <div class="container">{newArray}</div>
      </main>
    </>
  );
};
