import React from "react";

export const PartnerCard = ({ title, img, subtitle }) => {
  return (
    <div className="card">
      <img className="card__partner" src={img} alt="partner Logo" />
      <p className="card__title">{title}</p>
      {subtitle && <p className="card__subtitle">{subtitle}</p>}
    </div>
  );
};
