import React, { useState } from "react";
import { Modal } from "./Modal";

export const ModalCard = ({ img }) => {
  const [modalOpen, setModalOpen] = useState(false);

  return (
    <div className="card">
      <img className="card__license" src={img} alt="license" />
      <button
        onClick={() => setModalOpen(true)}
        className="card__button button"
      >
        
        <svg
          width="24"
          height="24"
          viewBox="0 0 24 24"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M17 12C17 11.712 16.8978 11.447 16.7035 11.2396L8.60532 2.29954C8.42127 2.10369 8.19632 2 7.93047 2C7.409 2 7 2.44931 7 3.04839C7 3.33641 7.10225 3.60138 7.26585 3.79724L14.7096 12L7.26585 20.2028C7.10225 20.3986 7 20.6521 7 20.9516C7 21.5507 7.409 22 7.93047 22C8.19632 22 8.42127 21.8963 8.60532 21.6889L16.7035 12.7604C16.8978 12.5415 17 12.288 17 12Z"
            fill="white"
          />
        </svg>
      </button>
      <Modal isOpened={modalOpen} onClose={() => setModalOpen(false)}>
      <img src={img} alt="license" />
      </Modal>
    </div>
  );
};
