import { useRef, useState } from "react";
import React from "react";
import cn from "classnames";

export function SliderDesktop({ navItems, slides }) {
  const sliderRef = useRef(null);
  const [slideNumber, setSlideNumber] = useState(0);

  function handleSlideChange(newSlideNumber) {
    if (sliderRef.current === null) {
      return;
    }

    setSlideNumber(newSlideNumber);
    const slideWidth = sliderRef.current.clientWidth;
    sliderRef.current.scrollTo({
      left: newSlideNumber * slideWidth,
      behavior: "smooth",
    });
  }

  return (
    <div className="slider">
      <ul className="slider__nav-items">
        {navItems.map(({ id, name, Icon }, idx) => (
          <li
            key={id}
            onClick={() => handleSlideChange(idx)}
            className={cn("slider__nav-item", {
              "slider__nav-item--active": slideNumber === idx,
            })}
          >
            <Icon
              className={cn("slider__icon", {
                "slider__icon--active": slideNumber === idx,
              })}
            />
            <p className="slider__nav-item-text">{name}</p>
          </li>
        ))}
      </ul>
      <div ref={sliderRef} className="slider__slides">
        {slides.map(({ id, title, subtitle, imageUrl }) => (
          <div key={id} className="slider__slide">
            <div
              className="slider__slide-image"
              style={{ backgroundImage: `url(${imageUrl})` }}
            ></div>
            <div className="slider__slide-content">
              <h3 className="slider__slide-content-title">{title}</h3>
              <p className="slider__slide-content-subtitle">{subtitle}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
